@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
/* The CSS used for styling the markdown preview */
#preview code, pre {
    background-color: rgb(254, 227, 203);
    padding: 2px;
}
#preview pre>code {
    padding: 0;
}
#preview pre {
    margin-right: 1em;
}
/* Links */
#preview a:link {
    color: rgb(0, 175, 239);
    text-decoration: none;
}
#preview a:visited {
    color: rgb(0, 107, 179);
}
#preview a:hover, #preview a:active {
    text-decoration: underline;
}

#preview blockquote {
    margin-left: 1em;
    border-left: 3px solid rgb(0, 175, 239);
    padding-left: 6px;
}

/* Table */
#preview table {
    border-collapse: collapse;
}
#preview th, #preview td {
    border: 2px solid #ddd;
    padding: 4px;
}
#preview tr:nth-child(even){background-color: #f2f2f2;}
#preview th {
    padding-top: 6px;
    padding-bottom: 6px;
    text-align: left;
    background-color: rgb(0, 175, 239);
    color: white;
}
*{box-sizing:border-box}html,body,#app,#app>div,#root,.App,.App>div{height:100%}header{border:2px solid gray;border-radius:2em;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:auto;margin-bottom:1em}header h1{margin:0.1em;color:white;font-family:"Open Sans",sans-serif;font-size:1.5em;font-weight:bold;background-image:linear-gradient(to right, #f58634, #00afef);border-radius:inherit;padding:0.3em 0.6em}#workspace{display:flex;flex-wrap:wrap;justify-content:center;height:80vh}#workspace div{flex-grow:1}#workspace #editorPane,#workspace #previewerPane{position:relative;margin:0 0.5em 0.5em 0.5em;height:100%;border-top-left-radius:1em;border-top-right-radius:1em}#workspace #editorPane #titleBar,#workspace #previewerPane #titleBar{position:absolute;color:white;top:0;right:0;padding:0.3em 0.6em 0.3em 2em;line-height:0;font-family:"Open Sans",sans-serif;font-size:0.7em;font-weight:normal;border-top-right-radius:1em;border-bottom-left-radius:5em}#workspace #editorPane #titleBar h2,#workspace #previewerPane #titleBar h2{display:inline-block}#workspace #editorPane #titleBar #actionButtons,#workspace #previewerPane #titleBar #actionButtons{float:right;margin-left:1em;margin-top:2px}#workspace #editorPane #titleBar #actionButtons .icon,#workspace #previewerPane #titleBar #actionButtons .icon{margin:2px;cursor:pointer;transition-property:opacity;transition-duration:0.1s;transition-delay:0}#workspace #editorPane #titleBar #actionButtons .icon:hover,#workspace #previewerPane #titleBar #actionButtons .icon:hover{opacity:0.7}#workspace #editor{width:100%;height:100%;overflow:auto;resize:none;padding-top:3em;padding-left:1em;border-top-right-radius:1em;border-top-left-radius:1em;border:none;font-size:1.1em}#workspace #editor:focus{outline:none}#workspace #preview{padding-top:3em;padding-left:1em;height:100%;overflow:auto}#workspace #previewerPane{box-shadow:0 4px 8px 0 rgba(0,175,239,0.2),0 6px 20px 0 rgba(0,175,239,0.19)}#workspace #previewerPane #titleBar{background-color:#00afef}#credits{font-family:"Open Sans", sans-serif;position:fixed;bottom:0.5em;right:0.5em;font-size:1em;font-weight:bold}#credits a{color:black;font-weight:normal;transition-property:color, letter-spacing;transition-duration:0.5s, 0.5s;transition-delay:0s, 0s}#credits a:nth-child(1){margin-left:1em}#credits a:link,#credits a:visited{text-decoration:none}#credits a:hover,#credits a:active{letter-spacing:2px}#credits #marked:hover,#credits #marked:active{color:#f58634}#credits #me:hover,#credits #me:active{color:#00afef}@media screen and (max-width: 360px){#workspace #editorPane #titleBar,#workspace #previewerPane #titleBar{padding:0.3em 0.3em 0.3em 1em;font-size:0.5em}#workspace #editorPane #titleBar #actionButtons,#workspace #previewerPane #titleBar #actionButtons{margin-left:0}}

